@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    h1 {
        @apply text-4xl font-bold  
    }
    h2 {
        @apply text-3xl font-bold
    }
    h3 {
        @apply text-2xl font-bold
    }
    h4 {
        @apply text-xl font-bold
    }
    h5 {
        @apply text-sm font-bold
    }
    h6 {
        @apply text-xs font-bold
    }
    .nav-link.active::before {
        @apply w-full
    }
    .drop-down-menu.active {
        @apply flex
    }
}